import * as React from 'react'
import { useState } from 'react'
import Layout from '../../components/layout'
import Card from '../../components/card'
import SearchBar from '../../components/searchbar'
import { graphql } from 'gatsby'
import { stringInArray } from '../../misc/utils'

const ProjectsPage = ({ data }) => {
  const [searchKeys, setSearchKeys] = useState([]);
  const [searchTags, setSearchTags] = useState([]);

  const searchBarChange = (e) => {
    console.log(searchKeys);
    setSearchKeys(e.target.value.split(/,| /).filter(Boolean));
  };

  const tagBarChange = (e) => {
    console.log(searchKeys);
    setSearchTags(e.target.value.split(/,| /).filter(Boolean));
  };

  const projectsFilter = (entry) => {
    if (!(searchKeys.length + searchTags.length)) {
      return true;
    }
    if (searchKeys.some((key) => entry.frontmatter.title.toLowerCase().includes(key.toLowerCase()))) {
      return true;
    }
    if (searchTags.some((tag) => stringInArray(tag, entry.frontmatter.tags))) {
      return true;
    }
    return false;
  };

  return (
    <>
      <Layout pageTitle="Projects">
        <div className="grid grid-cols-2 sm:grid-cols-5 gap-4 sm:gap-6">
          <div className="col-span-2 sm:col-span-1 sm:row-span-6 space-y-2 sm:space-y-3">
            <SearchBar onChangeFunc={searchBarChange} placeholderText="search by name"/>
            <SearchBar onChangeFunc={tagBarChange} placeholderText="search by tag"/>
          </div>
          {
            data.allMdx.nodes.filter((entry) => projectsFilter(entry)).map((node) => (
              <Card data={node} active={true}/>
            ))
          }
          { 
            data.allMdx.nodes.filter((entry) => !projectsFilter(entry)).map((node) => (
              <Card data={node} active={false}/>
            ))
          }
        </div>
      </Layout>
    </>
  )
}

export const query = graphql`
  query {
    allMdx(sort: {frontmatter: {date: DESC}}) {
      nodes {
        frontmatter {
          date(formatString: "MMMM D, YYYY")
          title
          slug
          thumbnail {
            childImageSharp {
              gatsbyImageData
            }
          }
          tags
          thumbnail_alt
        }
        excerpt
      }
    }
  }`

export const Head = () => <title>Projects</title>

export default ProjectsPage
