import * as React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import Tag from './tag'
import { classConcat } from '../misc/utils'
import { faTags } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Card = ({ data, active }) => {
  const thumbnail = getImage(data.frontmatter.thumbnail)
  const grayscale = active ? "grayscale-0" : "grayscale opacity-50";

  return (
    <div>
      <div className={classConcat(grayscale, "overflow-hidden rounded-lg bg-gray-100 border-gray-300 border transition ease-in-out shadow-sm hover:shadow-xl hover:-translate-y-2 hover:scale-[1.05] hover:opacity-100")}>
        <Link to={`/projects/${data.frontmatter.slug}`}>
          <div className="shadow">
            <GatsbyImage
              image={thumbnail}
              alt={data.frontmatter.thumnbnail_alt}
              className="h-full w-full object-cover"
              imgClassName=""
            />
          </div>

          <div className="text-gray-800 py-1 sm:py-2 px-1 sm:px-2 shadow">
            <p className="text-center text-sm sm:text-lg text-wrap font-bold tracking-tight">{data.frontmatter.title}</p>
          </div>

          <div className="py-1 sm:py-2 px-2 sm:px-4 flex items-center shadow text-gray-800">
            <FontAwesomeIcon icon={faTags} />
            <div className="w-full h-full flex flex-wrap items-center pl-2">
              {
                data.frontmatter.tags.map((tag) => (
                  <Tag text={tag}/>
                ))
              }
            </div>
          </div>
        </Link>
      </div>
    </div>
  )
}

export default Card