import * as React from 'react'
import { classConcat } from '../misc/utils'

export const styles = {
  "coding" : "bg-green-400 text-white",
  "mechanical" : "bg-gray-500 text-gray-100",
  "personal" : "bg-red-800 text-white",
  "caltech" : "bg-orange-500 text-orange-100",
  "misc" : "bg-green-800 text-black",
}

function tagStyle(text) {
  if (text in styles) {
    return styles[text.toLowerCase()]
  }
  return "bg-gray-200 text-black"
}

const Tag = ({ text }) => {
  return (
    <div className={classConcat(tagStyle(text), "rounded-md text-xs sm:text-sm mx-[1px] my-[1px]")}>
      <p className="px-1">{text}</p>
    </div>
  )
}

export default Tag